/* apply a natural box layout model to all elements */
*,
*:before,
*:after {
  box-sizing: border-box; }

@keyframes circular-ripple {
  0% {
    transform: translate(-50%, -50%) scale(0);
    opacity: 1; }
  25% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1; }
  30% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1; }
  100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0; } }

@keyframes circular-pulse {
  0% {
    box-shadow: 0 0 0 0 #fff;
    transform: scale(1); }
  25% {
    transform: scale(1.05); }
  50% {
    transform: scale(0.95); }
  75% {
    transform: scale(1); }
  100% {
    box-shadow: 0 0 1.25rem 1.25rem transparent; } }

.setup form:before, .setup form fieldset:before,
.setup form:after,
.setup form fieldset:after {
  content: ' ';
  display: table; }

.setup form:after, .setup form fieldset:after {
  clear: both; }

h1,
h2,
h3,
h4,
h5,
h6 {
  -webkit-tap-highlight-color: transparent;
  user-select: none; }

.test-outer .test, .test-outer .test .overlay-canvas {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%; }

