  a img {
    border: none; }

#site {
  overflow: hidden;
  width: 100%;
  height: 100vh; }

button {
  color: #fff;
  background-color: #d23654;
  border: none;
  outline: none; }

.setup {
  background: #131117;
  padding: 1rem 1rem 0; }
  .setup form fieldset {
    float: left;
    margin: 0 4rem 1rem 0;
    width: 15rem; }
    .setup form fieldset.left {
      clear: both; }
  .setup form label {
    display: block;
    margin-right: .75rem;
    width: 5rem;
    overflow: hidden;
    float: left; }
  .setup form input,
  .setup form output {
    background-color: #131117;
    color: #fff;
    border: none;
    border-bottom: 1px solid #fff;
    width: 8rem;
    line-height: 1.2;
    height: 1.2rem;
    float: left; }

.test-outer {
  position: relative;
  display: block;
  width: 100%;
  height: 80%;
  margin: 0 auto;
  overflow: hidden;
  background: #eee;
  background: #c5184d url(../images/magician.jpg) center/contain no-repeat; }
  .test-outer:before {
    content: ' ';
    display: block;
    padding-top: 100%; }
  .test-outer .test .clouds {
    position: absolute;
    left: -25%;
    top: 50%;
    display: block;
    width: 150%;
    height: auto;
    transform: translate(0%, -60%);
    opacity: .85; }
    .test-outer .test .clouds img {
      position: relative;
      display: block;
      width: 100%;
      height: auto; }
  .test-outer .test button {
    position: absolute;
    left: 1rem;
    top: .5rem;
    display: block;
    z-index: 10;
    background-color: #131117;
    transition: background-color .4s; }
    .test-outer .test button.clear {
      left: auto;
      right: 1rem; }
  .test-outer .test .overlay-canvas {
